@import 'variables.scss';
.hero {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  background-color: black;
  background-color: $primary-red;
  background-image: repeating-linear-gradient(45deg, transparent, transparent 80px, rgba(160,2,2,.5) 80px, rgba(160,2,2,.5) 160px);
  color: white;

  .main {
    max-width: 600px;
    padding: 30px 0;
    z-index: 1;
  }

  .video {
    position: fixed;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
  }

  h1, h2, h3 {
    color: white;
    font-weight: 900;
    font-size: 45px;
    text-align: center;
    letter-spacing: -2px;
  }

  h1.logo {
    margin: 40px 0;
  }

  h1.logo img {
    max-width: 300px;
  }

  h2 {
    font-size: 50px;
  }

  h3 {
    font-size: 30px;
  }

  .divider {
    width: 0px;
    height: 2px;
    margin: 10px auto;
    background-color: white;
    transition: all 1.5s ease-in-out;
  }
  .divider.show {
    width: 100%;
  }
  .divider.hidden {
    margin: 700px auto;
  }
  .roller {
    transition: all 1.5s ease-in-out;
    max-width: 600px;
  }

  .show-interest-button {
    background-color: white;
    color: $primary-red;
    border-radius: 7.5px;
    font-size: 30px;
    line-height: 60px;
    margin: 35px auto;
    transition: box-shadow .1s ease-in-out;
    text-align: center;
    width: 300px;
    height: 60px;

    p {
      font-weight: 300;
    }
  }

  .right-side.show {
  }
  .right-side-content.hidden {
    margin-top: 100vh;
  }
  .right-side-content.show-form {
    margin-top: -160px;
  }
}

@media (max-width: 868px){
  .section.hero {
    padding: 20px;

    .video {
      position: fixed;
      left: auto;
      right: auto;
      top: 0;
      min-width: 100vw;
      min-height: 100%;
      width: auto;
      height: 100%;
    }


    .main {
      margin-bottom: 15vh;
    }
    
    h1.logo {
      margin: 0;
    }

    h1.logo, h2 {
      color: #D6E9E5;
      font-weight: 600;
      font-size: 35px;
      text-align: center;
    }
    h1.logo img {
      max-width: 115px;
    }

    h2 {
      font-size: 14px;
    }
    h3 {
      font-size: 14px;
    }

    .divider {
      transition: none;
    }

    .divider.show {
      margin: 20px auto;
    }

    // .read-more-button-container {
    //   margin: 0;
    // }
  }
}
